import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Ring } from "awesome-react-spinners";

import "../style.css";

export default function Dashboard(props) {
  const token = JSON.parse(localStorage.getItem("token"));
  const [user, setUser] = useState([]);
  const [url, setUrl] = useState([]);
  const [newUrl, setNewUrl] = useState("");

  // const [pollCreated, setPollCreated] = useState(false);
  const onNewUrlChange = (val) => {
    setNewUrl(val);
  };
  const history = useHistory();

  const fetchData = async () => {
    try {
      const response = await axios.get(
        `http://localhost:3002/user/${token.id}`,
        {
          headers: {
            Authorization: `Bearer ${token.token}`,
          },
        }
      );

      setUser(response.data);
      setUrl(response.data.url);
    } catch (error) {
      const err = error.message.split(" ")[5];

      switch (err) {
        case "404":
          toast.error("No Polls Found", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: "false",
          });
          break;

        default:
          toast.error("Network error", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: "false",
          });
      }
    }
  };
  const shortenUrl = async () => {
    console.log("working");
    try {
      const response = await axios.post(
        `http://localhost:3002/${token.id}`,
        {
          fullUrl: newUrl,
        },
        {
          headers: {
            Authorization: `Bearer ${token.token}`,
          },
        }
      );

      console.log(response.data);
      window.location.reload();
    } catch (error) {
      console.log(error);
      const err = error.message.split(" ")[5];

      switch (err) {
        case "404":
          toast.error("No Polls Found", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: "false",
          });
          break;

        default:
          toast.error("Network error", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: "false",
          });
      }
    }
  };
  useEffect(() => {
    if (token === null) {
      history.push("/");
    }
    fetchData();

    //eslint-disable-next-line
  }, []);
  // const deletePoll = async (val) => {

  //   try {
  //     const response = await axios({
  //       url: `https://cyon-poll.herokuapp.com/poll`,
  //       method: "DELETE",
  //       data: { pollName: val },
  //       headers: {
  //         "content-type": "application/json",
  //         Authorization: `Bearer ${token.adminToken}`,
  //       },
  //     });
  //     setLoading(false);
  //     console.log(response);
  //     window.location.reload();
  //   } catch (error) {
  //     setLoading(false);
  //     console.log(error.message);
  //   }
  // };
  console.log(token);
  console.log(user);
  console.log(url);
  return (
    <div className="poll-container ">
      <div className="poll-body">
        <input
          type="text"
          className="input"
          placeholder="Add new URL"
          onChange={(e) => onNewUrlChange(e.target.value)}
        />
        <button
          className="input poll-create-poll-btn"
          style={{ alignSelf: "center" }}
          onClick={() => shortenUrl()}
        >
          Shorten
        </button>
        {url.map((item) => (
          <div key={item._id}>
            <p>Full: {item.fullUrl}</p>
            <p>
              Short:{" "}
              <a href={`http://localhost:3002/${item.shortUrl}`}>
                http://localhost:3002/{item.shortUrl}
              </a>
            </p>
            <p>Clicks:{item.clicks}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
